.nav {
    position: relative;
    padding-bottom: 5px;
}

.nav::before {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    border-bottom: 2px solid transparent;
    width: 0;
    transition: width 0.3s linear;

}

.nav:hover::before {
    border-bottom: 2px solid white;
        width: 100%;
}

