.hero {
    background-image: url('../../Assets/Image/background.jpg');
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
}

.scroll {
  position: absolute;
  left: 50%;
  bottom: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: scroll-anim 1s ease-in infinite;

   span {
    transform: rotate(180deg);
    opacity: 0.5;
    user-select: none;
   }
}

main article {
	-webkit-animation: scale-in-ver-center 0.8s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
	        animation: scale-in-ver-center 0.8s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}


 @-webkit-keyframes scale-in-ver-center {
    0% {
      -webkit-transform: scaleY(0);
              transform: scaleY(0);
      opacity: 1;
    }
    100% {
      -webkit-transform: scaleY(1);
              transform: scaleY(1);
      opacity: 1;
    }
  }
  @keyframes scale-in-ver-center {
    0% {
      -webkit-transform: scaleY(0);
              transform: scaleY(0);
      opacity: 1;
    }
    100% {
      -webkit-transform: scaleY(1);
              transform: scaleY(1);
      opacity: 1;
    }
  }

  @keyframes scroll-anim {
    0% {
      -webkit-transform: translateY(0);
              transform: translateY(0);

    }
    50% {
      -webkit-transform: translateY(5px);
              transform: translateY(5px);
    }
    100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
     
    }
  }
  
